export function initStep1(params) {
   const div = document.createElement("div");
   div.innerHTML = `
   <custom-header></custom-header>
   <div class="contenedor">
      <custom-text></custom-text>
   </div>
   <custom-footer></custom-footer>
   `;
   div.addEventListener("click", () => {
      params.goTo("/step1");
   });
   return div;
}
